<template>
  <div>
    <!--    面包屑导航-->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!--    卡片视图区域-->
    <el-card>
      <!--      搜索与添加区域-->
      <el-row :gutter="20">
        <el-col :span="10">
          <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="dialogVisible=true">添加用户</el-button>
        </el-col>
      </el-row>
      <!--      用户列表区域-->
      <el-table :data="userList" border>
        <el-table-column type="index" align="center"></el-table-column>
        <el-table-column prop="username" label="姓名" align="center"></el-table-column>
        <el-table-column prop="email" label="邮箱" align="center"></el-table-column>
        <el-table-column prop="mobile" label="电话" align="center"></el-table-column>
        <el-table-column prop="role_name" label="角色" align="center"></el-table-column>
        <el-table-column prop="mg_state" label="状态" align="center">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="userStateChange(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" width="180px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
            <el-tooltip effect="dark" content="分配角色" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="setUser(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--      分页区域-->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[1,3,5,10]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </el-card>
<!--    添加用户的对话框-->
    <el-dialog title="添加用户" :visible.sync="dialogVisible" width="30%" @close="addDialogClosed">
<!--      对话框内容主题区域-->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
    </el-dialog>
<!--    修改用户对话框-->
    <el-dialog title="修改用户" :visible.sync="editDialogVisible" width="30%" @close="editDialogClose">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="用户名">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUserInfo">确 定</el-button>
      </span>
    </el-dialog>
<!--    分配角色对话框-->
    <el-dialog title="分配角色" :visible.sync="setUserDialogVisible" width="50%" @close="setRoleDialogClose">
      <div>
        <p>当前用户:{{userInfo.username}}</p>
        <p>当前角色:{{userInfo.role_name}}</p>
        <p>分配新角色:
          <el-select v-model="selectRoleId" placeholder="请选择">
            <el-option
                v-for="item in rolesList"
                :key="item.id"
                :label="item.roleName"
                :value="item.id">
            </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setUserDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveRoleInfo">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "User",
  data() {
    // 邮箱验证
    let checkEmail=(rule,value,callback)=>{
      const regEmail=/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)){
        return callback()
      }
      callback(new Error('请输入合法的邮箱'))
    }
    // 手机号验证
    let checkMobile=(rule,value,callback)=>{
      const regMobile=/^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)){
        return callback()
      }
      callback(new Error('请输入合法手机号'))

    }
    return {
      // 获取用户列表参数对象
      queryInfo: {
        query: '',
        // 当前的页数
        pagenum: 1,
        // 当前每页显示多少数据
        pagesize: 3
      },
      userList: [],
      total: 0,
      // 控制添加用户对话框的显示隐藏
      dialogVisible:false,
      // 控制修改用户对话框的显示隐藏
      editDialogVisible:false,
      //添加用户表单数据
      addForm:{
        username:'',
        password:'',
        email:'',
        mobile:''
      },
      //添加用户表单数据验证规则
      addFormRules:{
        username:[
          {required: true, message: '请输入用户名称', trigger: 'blur'},
          {min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur'}
        ],
        password:[
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur'}
        ],
        email:[
          {required: true, message: '请输入邮箱', trigger: 'blur'},
          // {min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur'},
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile:[
          {required: true, message: '请输入手机号码', trigger: 'blur'},
          // {min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur'},
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 查询到的用户信息
      editForm:{},
      // 修改用户的验证规则
      editFormRules:{
        email:[
          {required: true, message: '请输入邮箱', trigger: 'blur'},
          // {min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur'},
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile:[
          {required: true, message: '请输入手机号码', trigger: 'blur'},
          // {min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur'},
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      //控制分配角色对话框的显示与隐藏
      setUserDialogVisible:false,
      //需要被分配权限的用户信息
      userInfo:{},
      //所有角色数据列表
      rolesList:[],
      //已选择角色id值
      selectRoleId:''
    }
  },
  created() {
    this.getUserList();
  },
  methods: {
    // 获取用户数据
    async getUserList() {
      const {data: res} = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200)
        return this.$message.error('获取用户列表失败')
      this.userList = res.data.users;
      this.total = res.data.total;
    },
    // 监听 pagesize改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    // 监听 页码值 改变事件1
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getUserList()
    },
    async userStateChange(userInfo) {
      const {data: res} = await this.$http.put(`users/${userInfo.id}/state/${userInfo.mg_state}`)
      if (res.meta.status !== 200) {
        userInfo.mg_state = !userInfo.mg_state
        return this.$message.error('更新用户数据失败！')
      }
      this.$message.success('更新用户状态成功')
    },
    //监听用户对话框的关闭事件
    addDialogClosed(){
      this.$refs.addFormRef.resetFields();
    },
    //点击按钮，添加新用户
    addUser(){
      this.$refs.addFormRef.validate(async valid=>{
        if (!valid)
          return
        const {data:res}=await this.$http.post('users',this.addForm)
        if (res.meta.status!==201){
          this.$message.error('添加用户失败')
        }
        this.$message.success('添加用户成功')
        //关闭对话框
        this.dialogVisible=false
        //重新获取数据
        this.getUserList()
      })
    },
    //展示修改用户编辑对话框
    async showEditDialog(id){
      const {data:res}=await this.$http.get('users/'+id)
      if(res.meta.status!==200){
        return this.$message.error('查询用户信息失败')
      }
      this.editForm=res.data
      this.editDialogVisible=true
    },
    //监听修改用户对话框的关闭事件
    editDialogClose(){
      this.$refs.editFormRef.clearValidate();
    },
    //修改用户数据并提交
    editUserInfo(){
      this.$refs.editFormRef.validate(async valid=>{
        if (!valid)
          return
        // 发起修改用户数据请求
        const {data:res}=await this.$http.put('users/'+this.editForm.id,{
          email:this.editForm.email,
          mobile:this.editForm.mobile
        })
        if (res.meta.status!==200){
          return this.$message.error('用户更新失败')
        }
        //关闭对话框
        this.editDialogVisible=false
        //提示更新用户信息成功
        this.$message.success('用户数据更新成功')
        //刷新数据列表
        this.getUserList();
      })
    },
    //根据用户id删除用户
    async removeUserById(id){
      // 删除提示
      const res=await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err=>err)
      //用户确认返回confirm 取消返回cancel
      // console.log(res)
      if (res!=='confirm'){
        return this.$message.info('已取消删除')
      }
      const {data:response}=await this.$http.delete('users/'+id)
      if (response.meta.status!==200){
        return this.$message.error('用户删除失败')
      }
      this.$message.success('该用户已删除')
      this.getUserList();
    },
    //展示分配角色对话框
    async setUser(userInfo){
      this.userInfo=userInfo
      //在展示对话框前获取所有角色列表
      const {data:res}=await this.$http.get('roles')
      if (res.meta.status!==200){
        return this.$message.error('获取角色列表失败')
      }
      this.rolesList=res.data
      this.setUserDialogVisible=true
    },
    //点击按钮分配角色
    async saveRoleInfo(){
      if(!this.selectRoleId){
        return this.$message.error('请选择需要分配的角色')
      }
      const {data:res}=await this.$http.put(`users/${this.userInfo.id}/role`,{
        rid:this.selectRoleId
      })
      if (res.meta.status!==200){
        return  this.$message.error('分配角色失败')
      }
      this.$message.success('更新角色成功')
      this.getUserList();
      this.setUserDialogVisible=false
    },
    // 监听分配角色对话框关闭事件
    setRoleDialogClose(){
      this.selectRoleId=''
      this.userInfo={}
    }
  }
}
</script>

<style lang="less" scoped>

</style>